/* eslint-disable @typescript-eslint/explicit-function-return-type */
/* eslint-disable @typescript-eslint/promise-function-async */
/* eslint-disable @typescript-eslint/naming-convention */
/* eslint-disable @typescript-eslint/return-await */
type _fantuanOptions = {
    urlBase?: string;
};
export const $fetch = async (url: string, x_options?: RequestInit & _fantuanOptions): Promise<any> => {
    const {urlBase = '', ...options} = x_options ?? {};
    return fetch(`${urlBase}/${url}`, options).then((raw) => {
        if (raw.ok && raw.status >= 200 && raw.status <= 299) {
            return raw.json();
        }
        return raw.json();
    });
};

const postConfig = {
    method: 'POST',
    mode: 'cors',
    headers: {
        'Content-Type': 'application/json',
    },
};
export const $post3rd = (url: string, data: any) => {
    return $fetch(url, {
        body: JSON.stringify(data),
        ...postConfig,
    } as RequestInit);
};
export const $postft = (url: string, data: any) => {
    return $fetch(url, {
        body: JSON.stringify(data),
        ...postConfig,
    } as RequestInit);
};

const getConfig = {
    method: 'GET',
    headers: {
        'Content-Type': 'application/json',
    },
};
export const $get3rd = (url: string) => {
    return $fetch(url, getConfig);
}