/* eslint-disable @typescript-eslint/array-type */
/* eslint-disable @typescript-eslint/prefer-ts-expect-error */
/* eslint-disable @typescript-eslint/explicit-function-return-type */
/* eslint-disable react/no-array-index-key */
/* eslint-disable @typescript-eslint/no-non-null-assertion */
import React from 'react';
import RelativeBox from '../../components/RelativeBox';
import Box, {BoxProps} from '@mui/material/Box';
// import {CategoryItem} from '@/types';
import MainTabSection from './MainTabSection';
import SecondaryTabSection from './SecondaryTabSection';

const CategoryTabs: React.FC<CategoryTabsProps> = ({tabsData, TabsRef, initSelection = [0, 0], onTabChange, hideSecondaryTabs, options, ...rest}) => {
    const TabsBoxRef = React.useRef(null);
    const [selectionChanged, setSelectionChanged] = React.useState<number>(0);
    const updateTabs = (): void => {
        // NOTE: memo sub components
        setSelectionChanged(Math.random() * Math.random() * 100000);
    };
    const selectionRefObj = React.useRef<CategoryTabsSelectionRefType>({
        first: initSelection?.[0] || 0,
        secondary: Array.isArray(tabsData) ? tabsData.map((mainTab, mainIndex) => (mainIndex === initSelection?.[1] ? initSelection?.[1] : 0)) : [],
    });
    const selection = selectionRefObj.current;
    const getExposedProps = (indexes?: [number, number | undefined]) => {
        if (Array.isArray(indexes)) {
            return {
                indexes,
                params: getParamsFromCategoryByIndexes(indexes, tabsData),
                // @ts-ignore
                element: TabsBoxRef?.current?.getBoundingClientRect?.(),
            };
        }
        const currentIndexes = [selection.first, selection.secondary?.[selection.first]] as any;
        return {
            indexes: currentIndexes,
            params: getParamsFromCategoryByIndexes(currentIndexes, tabsData),
            // @ts-ignore
            element: TabsBoxRef?.current?.getBoundingClientRect?.(),
        };
    };
    React.useEffect(() => {
        onTabChange?.(getExposedProps() as any);
    }, []);
    // TODO: 
    React.useEffect(() => {
        // selectionRefObj.current = {
        //     first: initSelection?.[0] || 0,
        //     secondary: Array.isArray(tabsData) ? tabsData.map((mainTab, mainIndex) => (mainIndex === initSelection?.[1] ? initSelection?.[1] : 0)) : [],
        // };
        // updateTabs();
    }, [tabsData]);
    const onMainTabChange = (e: any, v: number) => {
        selection.first = v;
        updateTabs();
        const indexes = [v, selection.secondary?.[v]] as any;
        onTabChange?.(getExposedProps(indexes), {scrollToTabBar: true});
    };
    const onSecondaryTabChange = (e: any, v: number | undefined) => {
        selection.secondary[selection.first] = v;
        updateTabs();
        const indexes = [selection.first, v] as any;
        onTabChange?.(getExposedProps(indexes), {scrollToTabBar: true});
    };
    const getCurrentTabIndexes = () => [selection.first, selection.secondary[selection.first]] as any;
    const getNextTabIndexes = (tabIndexes?: TabSharedProps['indexes']): TabSharedProps['indexes'] => {
        const tabIds = tabIndexes ?? [selection.first, selection.secondary?.[selection.first]];
        const mainLength = tabsData?.length ?? 0;
        const tabVal = Number(tabIds?.[0]);
        const sub = Number(tabIds?.[1]) || 0;
        const subLength = tabsData?.[tabVal]?.secondCategoryList?.length || 0;
        const subExisted = Array.isArray(tabsData?.[tabVal]?.secondCategoryList);
        let nextV = 0;
        let nextS;
        if (subExisted) {
            // @ts-ignore
            if (sub + 1 < subLength) {
                nextV = tabVal;
                nextS = sub + 1;
            } else if (tabVal + 1 < mainLength) {
                nextV = tabVal + 1;
                const nextSubExisted = Array.isArray(tabsData?.[nextV]?.secondCategoryList);
                if (nextSubExisted) {
                    nextS = 0;
                }
            }
        } else if (tabVal + 1 < mainLength) {
            nextV = tabVal + 1;
            const nextSubExisted = Array.isArray(tabsData?.[nextV]?.secondCategoryList);
            if (nextSubExisted) {
                nextS = 0;
            }
        }
        return [nextV, nextS];
    };
    const getPrevTabIndexes = (tabIndexes?: TabSharedProps['indexes']): TabSharedProps['indexes'] => {
        const tabIds = tabIndexes ?? [selection.first, selection.secondary?.[selection.first]];
        const sub = Number(tabIds?.[1]) || 0;
        const tabVal = Number(tabIds?.[0]);
        const subExisted = Array.isArray(tabsData?.[tabVal]?.secondCategoryList);
        let prevV = 0;
        let prevS;
        if (subExisted) {
            // @ts-ignore
            if (sub - 1 >= 0) {
                prevV = tabVal;
                prevS = sub - 1;
            } else if (tabVal - 1 >= 0) {
                prevV = tabVal - 1;
                const prevSubExisted = Array.isArray(tabsData?.[prevV]?.secondCategoryList);
                if (prevSubExisted) {
                    prevS = 0;
                }
            }
        } else if (tabVal - 1 >= 0) {
            prevV = tabVal - 1;
            const prevSubExisted = Array.isArray(tabsData?.[prevV]?.secondCategoryList);
            if (prevSubExisted) {
                prevS = 0;
            }
        }
        return [prevV, prevS];
    };
    const switchNext = () => {
        const next = getNextTabIndexes();
        // eslint-disable-next-line prefer-destructuring
        selection.first = next[0];
        if (Number.isInteger(next[1])) {
            // eslint-disable-next-line prefer-destructuring
            selection.secondary[selection.first] = next[1];
        }
        updateTabs();
        onTabChange?.(getExposedProps(), {scrollToTabBar: true});
    };
    const switchPrev = () => {
        const prev = getPrevTabIndexes();
        // eslint-disable-next-line prefer-destructuring
        selection.first = prev[0];
        if (Number.isInteger(prev[1])) {
            // eslint-disable-next-line prefer-destructuring
            selection.secondary[selection.first] = prev[1];
        }
        updateTabs();
        onTabChange?.(getExposedProps(), {scrollToTabBar: true});
    };
    const switchToTabByCateId = (firstCategoryId: number, secondCategoryId: number) => {
        if (typeof firstCategoryId !== 'number' || typeof secondCategoryId !== 'number') {
            console.warn('WARN(switchToTabByCateId): number', {firstCategoryId, secondCategoryId});
            return;
        }
        const firstIndex = tabsData?.findIndex?.((fi) => {
            return String(fi.firstCategoryId) === String(firstCategoryId);
        });
        if (Number.isInteger(firstIndex) && typeof firstIndex === 'number') {
            const firstTab = (tabsData as any)[firstIndex];
            if (typeof firstTab === 'object') {
                const secondIndex = firstTab?.secondCategoryList?.findIndex((sec: any) => {
                    return String(sec.secondCategoryId) === String(secondCategoryId);
                });
                if (Number.isInteger(secondIndex) && typeof secondIndex === 'number') {
                    selection.first = firstIndex;
                    selection.secondary[selection.first] = secondIndex;
                }
            }
        }
        updateTabs();
        onTabChange?.(getExposedProps(), {scrollToTabBar: true});
    };

    React.useEffect(() => {
        if (typeof TabsRef === 'object') {
            TabsRef.current = {
                switchNext,
                switchPrev,
                switchToTabByCateId,
                getNextTabIndexes,
                getPrevTabIndexes,
                getCurrentTabIndexes,
                getExposedProps,
            };
        }
    }, [selectionChanged]);
    const renderPlaceHolder = React.useMemo(() => {
        // TODO: unify usage, 38px
        // @ts-ignore
        if (tabsData?.[selection.first]?.secondCategoryList?.length > 0) return <Box sx={{height: '38px'}} />;
        return null;
    }, [tabsData, selectionChanged]);
    return tabsData?.length ? (
        <Box {...rest} ref={TabsBoxRef} sx={{p: '9px 15px 8px 15px'}}>
            <MainTabSection data={tabsData} selected={selection.first} onChange={onMainTabChange} options={options} />
            {hideSecondaryTabs ? null : (
                <RelativeBox>
                    <SecondaryTabSection data={tabsData} selectedMainTab={selection.first} selections={selection.secondary} onChange={onSecondaryTabChange} options={options} />
                </RelativeBox>
            )}
            {renderPlaceHolder}
        </Box>
    ) : null;
};

export default CategoryTabs;
// export default React.memo(CategoryTabs, (prev, next) => prev.tabsData === next.tabsData && prev.hideSecondaryTabs === next.hideSecondaryTabs);

// TODO: types
const getParamsFromCategoryByIndexes = (indexes: [number, number | undefined], firstCategory: any[] | undefined) => {
    if (!firstCategory || firstCategory.length === 0) return null;
    const firstCategoryId = firstCategory[indexes[0]]?.firstCategoryId;
    const secondCategoryId = firstCategory[indexes[0]]?.secondCategoryList?.[indexes[1] as number]?.secondCategoryId;
    return {firstCategoryId, secondCategoryId};
};

export type OnTabChangeFuncType = (props: TabSharedProps, options?: {scrollToTabBar?: boolean}) => void;
export type CategoryTabsProps = {
    tabsData: any[] | undefined; // TODO: categoryTYpe
    initSelection?: [id: number, subId: number | undefined];
    onTabChange: OnTabChangeFuncType;
    hideSecondaryTabs?: boolean;
    TabsRef?: React.MutableRefObject<TabsRefObjectType>;
    options?: {
        generateOnClickTabItem?: Function;
    };
} & BoxProps;
export type TabSharedProps = {
    indexes: [number, number | undefined];
    params: {firstCategoryId: number; secondCategoryId: number | undefined} | null;
    element?: {height: number};
    scrollToTabBar?: boolean;
};
export type TabsRefObjectType = {
    switchNext: () => void;
    switchPrev: () => void;
    switchToTabByCateId: (firstCategoryId: number, secondCategoryId: number) => void;
    getNextTabIndexes: (tabIndexes: TabSharedProps['indexes']) => TabSharedProps['indexes'];
    getPrevTabIndexes: (tabIndexes: TabSharedProps['indexes']) => TabSharedProps['indexes'];
    getCurrentTabIndexes: () => TabSharedProps['indexes'];
    getExposedProps: (tabIndexes?: TabSharedProps['indexes']) => TabSharedProps;
};
export type CategoryTabsSelectionRefType = {
    first: number;
    secondary: (number | undefined)[];
};
