import * as React from 'react';
import Text from '../../components/Text';
import {styled} from '@mui/material/styles';
import Box from '@mui/material/Box';
import Media, {MediaProps} from '../../components/Media';
import {useHistory} from 'react-router-dom';
import {useRouteInfo} from '@/routing/useRouteInfo';
import {RenderComponentProps} from 'masonic';

type MasonryItemType = {
    dish: {
        dishName: string;
        dishDesc: string;
        imgFilePath: string;
        imgPlaceholderFilePath: string;
        isAnchor: boolean;
        dishId: number;
    };
    derived: {
        elementId: string;
    };
};

export type MenuItemCardProps = RenderComponentProps<MasonryItemType>;

const ELEMENT_MARGIN_BOTTOM_REM = 0.5;

const MenuItemCard = (masonryItemProps: MenuItemCardProps) => {
    // eslint-disable-next-line @typescript-eslint/consistent-type-assertions
    const {dish, derived, ...cardProps} = masonryItemProps?.data || {};
    const {dishName, dishDesc, imgFilePath, imgPlaceholderFilePath, dishId} = dish;
    const {sx: propsSx, ...rest} = cardProps as any;
    const history = useHistory();
    const routeInfo = useRouteInfo();
    const generateClickUrl = () => {
        // eslint-disable-next-line @typescript-eslint/restrict-template-expressions
        history.push(`/store/${(routeInfo.routeParams as any)?.storeId}/detail-dish/${dishId}`, dish);
    };
    const dynStyles = React.useMemo(() => {
        return {
            headImageWrapper: !imgFilePath
                ? {
                      backgroundColor: 'black',
                      height: '100px',
                  }
                : {},
            headImage: !imgFilePath
                ? {
                      opacity: 0.4,
                  }
                : undefined,
        };
    }, [imgFilePath]);
    const emptyHintText = !imgFilePath ? 'No picture yet' : null;
    return (
        <Box id={derived.elementId} sx={{boxShadow: 'unset', mb: 1, ...propsSx}} {...rest}>
            <Box sx={{position: 'relative', borderRadius: '6px', mb: 1, overflow: 'hidden', ...dynStyles.headImageWrapper}} onClick={generateClickUrl}>
                <HeadImage image={imgFilePath} imagePlaceholder={imgPlaceholderFilePath} style={dynStyles.headImage} />
                {emptyHintText ? (
                    <Box
                        style={{
                            position: 'absolute',
                            top: 0,
                            left: 0,
                            width: '100%',
                            height: '100%',
                            display: 'flex',
                            flexDirection: 'row',
                            alignItems: 'center',
                            justifyContent: 'center',
                            zIndex: 10,
                            color: 'white',
                            fontSize: '10px',
                            fontWeight: 400,
                            lineHeight: '14px',
                            fontFamily: `"Roboto","Helvetica","Arial",sans-serif`,
                        }}>
                        {emptyHintText}
                    </Box>
                ) : null}
            </Box>
            <Title lineClamp={2} sx={{mb: ELEMENT_MARGIN_BOTTOM_REM}}>
                {dishName}
            </Title>
            <Description>{dishDesc}</Description>
        </Box>
    );
};

const HeadImage = styled((props: MediaProps) => <Media alt="Goods Image" loading="lazy" {...props} />)(() => ({
    width: '100%',
    height: '100%',
    objectFit: 'cover',
}));
const Title = styled(Text)(({theme}) => ({
    padding: '0 5px 0 5px',
    fontWeight: theme.typography.fontWeightBold,
    fontSize: theme.typography.pxToRem(14),
    lineHeight: theme.typography.pxToRem(20),
}));
const Description = styled(Text)(({theme}) => ({
    padding: '0 5px 0 5px',
    fontWeight: theme.typography.fontWeightRegular,
    fontSize: theme.typography.pxToRem(12),
    lineHeight: theme.typography.pxToRem(17),
}));
const SoldOutTxt = styled(Text)(({theme}) => ({
    marginTop: 10,
    marginBottom: 10,
    fontWeight: theme.typography.fontWeightBold,
    color: theme.palette.common.white,
    lineHeight: 1,
    textAlign: 'center',
    fontSize: theme.typography.pxToRem(16),
}));

export default React.memo(MenuItemCard);
