import React from 'react';
import EMPTY_IMAGE_PLACEHOLDER from '../assets/images/empty_image_placeholder.png';
import CardMedia, {CardMediaProps} from '@mui/material/CardMedia';

function ParseImageSrc(props: {src: string | undefined | null; compress?: {width: number; height: number}}) {
    // 
    if (!props?.src) {
        return EMPTY_IMAGE_PLACEHOLDER;
    }
    if (props.src?.indexOf('data:image') === 0) {
        return props.src;
    }
    if (props.src?.indexOf('/static') === 0) {
        return props.src;
    }
    // 
    return props.src;
}

export type MediaProps = CardMediaProps<'img'> & {imagePlaceholder?: string; compress?: {width: number; height: number; quality?: number; mode?: 'lfit' | 'fixed'}};
const Media: React.FC<MediaProps> = ({image, compress, imagePlaceholder, ...rest}) => {
    const [err, setErr] = React.useState();
    const currentImage = image ? image : imagePlaceholder ? imagePlaceholder : undefined;
    if (!currentImage) {
        return <CardMedia component="img" alt="void" image={EMPTY_IMAGE_PLACEHOLDER} alternative-src={EMPTY_IMAGE_PLACEHOLDER} onError={setErr as any} {...rest} />;
    }
    return <CardMedia component="img" alt="image" loading="lazy" image={currentImage} alternative-src={image} onError={setErr as any} {...rest} />;
};

export default Media;
