import StorePage from '@/pages/store/StorePage';
import DetailDishPage from '@/pages/dish/DishPage';

export const routes = [
    {
        name: 'StorePage',
        path: '/store/:storeId',
        exact: true,
        isLazy: false,
        component: StorePage,
    },
    {
        name: 'DetailDishPage',
        path: '/store/:storeId/detail-dish/:dishId',
        exact: true,
        isLazy: false,
        component: DetailDishPage,
    },
];
