import {Box, BoxProps, styled, SxProps} from '@mui/material';
import React, {CSSProperties} from 'react';
import FANCY_PLACEHOLDER from '@/assets/images/fancy_placeholder.jpeg';
import Media from '@/components/Media';
import TextLineClampable from '@/components/Text';
import {StoreDataType} from '@/types/store-types';

const CoverSection = (props: BoxProps): any => {
    const {children, ...boxProps} = props;
    return (
        <Box {...boxProps}>
            {props.children}
            <Box style={{position: 'absolute', height: '100%', width: '100%', opacity: 0.68, backgroundColor: 'black'}} />
        </Box>
    );
};

const StoreCoverSection = (props: {store?: StoreDataType} & BoxProps): any => {
    const {children, store, ...rest} = props;
    const iconImg = store?.data ? `${store?.data?.imgFolderPath}/${store?.data.logoImgFileName}` : undefined;
    const coverImg = store?.data ? `${store?.data?.imgFolderPath}/${store?.data.coverImgFileName}` : undefined;
    return (
        <CoverSection style={{height: 250, position: 'relative', ...alignCenter}} {...rest}>
            <Box sx={{height: 90, mx: 3, ...alignCenter}}>
                {iconImg ? <Media image={iconImg} sx={{zIndex: 10, height: '50px', width: '50px'}} /> : null}
                <RowBox sx={{fontSize: 20, lineHeight: '28px', fontWeight: 600, mt: '12px', mb: '4px'}}>{store?.data.storeName}</RowBox>
                <RowBox sx={{fontSize: 14, lineHeight: '20px', fontWeight: 400, textAlign: 'center'}}>{store?.data.storeDesc}</RowBox>
            </Box>
            {coverImg ? <Media image={coverImg} sx={{position: 'absolute'}} /> : null}
        </CoverSection>
    );
};

const alignCenter: CSSProperties = {
    display: 'flex',
    alignContent: 'center',
    flexDirection: 'row',
    flexWrap: 'wrap',
    alignItems: 'center',
    justifyContent: 'center',
};
const RowBox = styled(TextLineClampable)({
    width: '100%',
    color: '#FFFFFF',
    zIndex: 21,
    ...alignCenter,
});

export default StoreCoverSection;
