import React from 'react';
import {Masonry, MasonryProps, MasonryScrollerProps} from 'masonic';
import {Box} from '@mui/material';

type WaterfallViewPropsType = {} & MasonryProps<any>;
type WaterfallReturnType = React.FunctionComponentElement<MasonryScrollerProps<unknown>>;
const WaterfallView = (props: WaterfallViewPropsType): WaterfallReturnType => {
    const {...masonryProps} = props;
    return (
        <Box sx={{mx: '5px'}}>
            <Masonry {...masonryProps} />
        </Box>
    );
};

export default WaterfallView;
