import React from 'react';
import {styled} from '@mui/material/styles';
import {Box, Dialog, CardMedia, SxProps} from '@mui/material';
import SvgIcon from '@/components/SvgIcon';
import {useHistory} from 'react-router-dom';
import TextLineClampable from '@/components/Text';
import {useGesture} from '@use-gesture/react';

interface DetailDishProps {
    dishId: number;
    dishName: string;
    dishDesc: string;
    imgFilePath?: string;
}

const ImgWithGesture = ({detailDishData}: {detailDishData: DetailDishProps}): JSX.Element => {
    const history = useHistory();
    const [openDialog, setOpen] = React.useState(false);
    const {dishName, dishDesc, imgFilePath} = detailDishData;
    const goBackHandler = () => {
        history.go(-1);
    };
    return (
        <Box>
            <Box sx={{position: 'relative'}}>
                <StyledSvgWrapper
                    sx={{top: 20, left: 11, justifyContent: 'flex-start', position: imgFilePath ? 'absolute' : 'unset', margin: imgFilePath ? null : '20px 0 0 11px'}}
                    onClick={goBackHandler}>
                    <SvgIcon name="go_back_arrow" size={44} />
                </StyledSvgWrapper>
                {imgFilePath ? (
                    <StyledSvgWrapper sx={{bottom: 12, right: 12, justifyContent: 'flex-end'}}>
                        <SvgIcon
                            name="magnify"
                            size={24}
                            onClick={() => {
                                setOpen(true);
                            }}
                        />
                    </StyledSvgWrapper>
                ) : null}
                {imgFilePath ? (
                    <StyledMedia
                        imageUrl={imgFilePath}
                        onClick={() => {
                            setOpen(true);
                        }}
                    />
                ) : null}
            </Box>
            <StyledTitle>{dishName}</StyledTitle>
            <StyledDesc>{dishDesc}</StyledDesc>
            <Dialog
                fullScreen
                open={openDialog}
                onClose={() => {
                    setOpen(false);
                }}
                PaperProps={{
                    style: {
                        display: 'flex',
                        justifyContent: 'center',
                    },
                }}>
                <Box sx={{overflow: 'hidden', position: 'relative', height: '100%'}}>
                    <StyledSvgWrapper
                        sx={{top: 20, left: 11, justifyContent: 'flex-start'}}
                        onClick={() => {
                            setOpen(false);
                        }}>
                        <SvgIcon name="go_back_arrow" size={44} />
                    </StyledSvgWrapper>
                    <Box sx={{height: '100%', backgroundColor: '#000000'}}>{imgFilePath ? <StyledGestureMeida imgFilePath={imgFilePath} /> : null}</Box>
                </Box>
            </Dialog>
        </Box>
    );
};

const StyledSvgWrapper = styled('div')({
    position: 'absolute',
    display: 'flex',
    zIndex: 20,
});
const StyledTitle = styled(TextLineClampable)(({theme}) => ({
    fontSize: theme.typography.pxToRem(20),
    lineHeight: theme.typography.pxToRem(28),
    fontWeight: 600,
    color: '#333333',
    padding: theme.typography.pxToRem(15),
    pb: theme.typography.pxToRem(12),
    boxSizing: 'border-box',
}));
const StyledDesc = styled(TextLineClampable)(({theme}) => ({
    fontSize: theme.typography.pxToRem(12),
    lineHeight: theme.typography.pxToRem(17),
    fontWeight: 400,
    color: '#333333',
    padding: '0 15px',
    boxSizing: 'border-box',
}));
export default ImgWithGesture;

const GestureImg = ({imgFilePath, ...rest}: {imgFilePath: string; sx?: SxProps}) => {
    const pinchRef = React.useRef<any>();
    const [gestureParam, setGestParam] = React.useState({dx: 0, dy: 0, scale: 1});
    useGesture(
        {
            onPinch: ({offset: [d]}) => {
                if (d >= 1) {
                    setGestParam((state) => ({...state, scale: d}));
                } else {
                    setGestParam((state) => ({...state, scale: 1}));
                }
            },
            onDrag: ({offset: [dx, dy]}) => {
                setGestParam((state) => ({...state, dx, dy}));
            },
        },
        {
            target: pinchRef,
            eventOptions: {passive: false},
        },
    );
    return (
        <GestureImgWrapper
            style={{touchAction: 'none', transform: `scale(${gestureParam.scale})`, left: gestureParam.dx, top: gestureParam.dy, position: 'relative'}}
            ref={pinchRef}>
            <CardMedia component="img" alt="dish-image" loading="eager" src={imgFilePath} {...rest} style={{height: 'auto'}} />
        </GestureImgWrapper>
    );
};
const GestureImgWrapper = styled('div')({
    height: '100%',
    display: 'flex',
    alignItems: 'center',
});
const StyledGestureMeida = styled(GestureImg)({
    width: '100%',
    height: '100%',
});

const DetailPageHeadImg = ({imageUrl, ...rest}: {imageUrl: string; sx?: SxProps; onClick?: () => void}) => {
    return <CardMedia component="img" alt="dish-image" loading="eager" src={imageUrl} {...rest} />;
};
const StyledMedia = styled(DetailPageHeadImg)({
    width: '100%',
    height: '50%',
    backgroundSize: 'cover',
    backgroundPosition: 'center',
});
