import React from 'react';
import * as icons from '@/assets/svg/index';
import {IconNames} from '@/assets/svg/index';

const SvgIcon: React.FC<{name: IconNames; size?: number; onClick?: () => void}> = ({name, size, ...rest}) => {
    const svgSrc = icons?.[name];
    const svgWidth = size ?? '100%';
    const svgHeight = size ?? '100%';
    return <img src={svgSrc} alt="svg-icon" style={{width: svgWidth, height: svgHeight}} {...rest} />;
};
export default SvgIcon;
