import React from 'react';
import ImgWithGesture from '@/components-bizz/dish/ImgWithGesture';
import {useLocation} from 'react-router-dom';

interface LocationStateType {
    dishId: number;
    dishName: string;
    dishDesc: string;
    imgFilePath?: string;
}
const DetailDishPage = (): React.ReactElement => {
    const location = useLocation<LocationStateType>();
    return <ImgWithGesture detailDishData={location?.state} />;
};

export default DetailDishPage;
