/* eslint-disable @typescript-eslint/prefer-ts-expect-error */
/* eslint-disable @typescript-eslint/array-type */
/* eslint-disable @typescript-eslint/restrict-template-expressions */
/* eslint-disable react/no-array-index-key */
/* eslint-disable react/jsx-fragments */
/* eslint-disable react/jsx-no-useless-fragment */
/* eslint-disable prettier/prettier */

import {styled, SxProps} from '@mui/material/styles';
import React, {SyntheticEvent} from 'react';
import Tabs, {TabsProps} from '@mui/material/Tabs';
import Tab, {TabProps} from '@mui/material/Tab';

const SecondaryTabSection: React.FC<{
    selectedMainTab: number;
    selections: (number | undefined)[]; // Tabs
    data: any[] | undefined; // TODO
    onChange: (event: React.SyntheticEvent<Element, Event>, value: any) => void;
    options?: {
        generateOnClickTabItem?: Function;
    };
}> = (props) => {
    const {selectedMainTab, selections, data, onChange, options} = props;
    return (
        <React.Fragment>
            {data?.map((item, index) => {
                const key = `secondary-tabs-${index}-subcategory-${item.id}`;
                const shouldShow = selectedMainTab === index;
                const isSelected = shouldShow ? selections?.[selectedMainTab] ?? false : false;
                return (
                    <SecondaryTabs
                        show={shouldShow}
                        selected={isSelected}
                        secondCategoryList={item.secondCategoryList}
                        onChange={onChange}
                        key={key}
                        className={key}
                        options={options}
                    />
                );
            })}
        </React.Fragment>
    );
};

export default SecondaryTabSection;

type SecondaryTabsProps = {
    show: boolean;
    selected: number | false;
    secondCategoryList: any[] | null;
    onChange: (event: React.SyntheticEvent<Element, Event>, value: any) => void;
    options?: {
        generateOnClickTabItem?: Function;
    };
} & TabsProps;
const SecondaryTabs: React.FC<SecondaryTabsProps> = ({show, selected, secondCategoryList, onChange, options, ...rest}) => {
    const onClickActions = secondCategoryList?.map((item) => {
        if (typeof options?.generateOnClickTabItem !== 'function') {
            return undefined;
        }
        return options.generateOnClickTabItem(item?.firstCategoryId, item?.secondCategoryId);
    });
    return (
        <Panel show={Number(show)}>
            <StyledTabs {...rest} value={selected} onChange={onChange} variant="scrollable" scrollButtons="auto" TabIndicatorProps={{style: {display: 'none'}}}>
                {secondCategoryList?.map((_item, i, arr) => (
                    // @ts-ignore
                    <StyledTab key={`second-tab-${_item?.firstCategoryId}-${_item?.secondCategoryId}_${i}`} label={_item.name} onClick={onClickActions?.[i]} />
                ))}
            </StyledTabs>
        </Panel>
    );
};

const Panel = styled('div')((props: {show: number}) => ({
    position: 'absolute',
    width: '100%',
    visibility: props.show ? 'visible' : 'hidden',
    zIndex: props.show ? 2 : 1,
}));

const StyledTabs = styled(Tabs)(({theme}) => ({
    minHeight: 'auto',
}));

const StyledTab = styled((props: TabProps) => <Tab {...props} disableRipple />)(({theme}) => ({
    color: 'black',
    padding: '0 10px',
    margin: '0 4px',
    lineHeight: '14px',
    fontWeight: theme.typography.fontWeightRegular,
    minWidth: 'auto',
    height: '30px',
    minHeight: '30px',
    borderRadius: 16,
    display: 'inline-flex',
    alignContent: 'center',
    backgroundColor: theme.palette.grey[200],
    fontSize: theme.typography.pxToRem(10),
    '&.Mui-selected': {
        color: theme.palette.common.white,
        backgroundColor: '#13C2C2',
        fontWeight: theme.typography.fontWeightMedium,
    },
    '&.Mui-focusVisible': {
        backgroundColor: 'transparent',
    },
}));
