/* eslint-disable @typescript-eslint/restrict-template-expressions */
import React from 'react';
import Page from '../../layouts/Page';
import {$get3rd} from '../../http';
import {useRouteInfo} from '../../routing/useRouteInfo';
import WaterfallView from '../../components-bizz/WaterfallView/WaterfallView';
import MenuItemCard from '../../components-bizz/cards/MenuItemCard';
import TabSection, {OnTabChangeFuncType, TabSharedProps} from '../../components-bizz/TabSection/TabSection';
import MockStoreCoverSection from '../../components-bizz/store/StoreCoverSection';
import StickyTopSection from '../../components/StickyTopSection';
import {StoreDataType, StoreDishDataType} from '@/types/store-types';
interface menu_data {
    waterfall_dish_list: [{dishName: string; dishDesc: string; imgFilePath: string}];
}
type DishDataType = {
    data: {
        menu_data: {};
        category_tab_data: any;
    };
};
const StorePage = (): React.ReactElement => {
    const routeInfo = useRouteInfo();
    const [RestResponse, setRestResponse] = React.useState<StoreDataType>();
    const [RestMenuResponse, setRestMenuResponse] = React.useState<StoreDishDataType>();
    const waterfall_dish_list =
        RestMenuResponse?.data?.menu_data?.waterfall_dish_list?.map((dish: any) => {
            return {
                dish: {
                    dishName: `${dish.dishNameEn} ${dish.dishNameZhCN} | $${dish.price}`,
                    dishDesc: `${dish.dishDescEn} ${dish.dishDescZhCN}`,
                    imgFilePath: dish.imgFileName ? `${dish.imgFolderPath}/${dish.imgFileName}` : undefined,
                    imgPlaceholderFilePath: RestResponse?.data ? `${RestResponse?.data.imgFolderPath}/${RestResponse?.data.coverImgFileName}` : undefined,
                    isAnchor: dish?.isAnchor,
                    dishId: dish?.dishId,
                },
                derived: {
                    elementId: `RS${dish.storeId}_FI${dish.firstCategoryId}_SEC${dish.secondCategoryId}_DI${dish.dishId}`,
                },
                info: {
                    firstCategoryId: dish.firstCategoryId,
                    secondCategoryId: dish.secondCategoryId,
                },
            };
        }) ?? [];
    const anchor_list = waterfall_dish_list
        ?.map((viewData: any, index: number) => ({
            index,
            elementId: viewData?.derived?.elementId,
            isAnchor: viewData?.dish?.isAnchor,
            firstCategoryId: viewData?.info.firstCategoryId,
            secondCategoryId: viewData?.info.secondCategoryId,
        }))
        ?.filter((vd: any) => !!vd?.isAnchor);

    const category_tab_data = RestMenuResponse?.data?.category_tab_data;

    // ===== onRender =====
    // NOTE: e window
    const disable_scroll_linkage_timer = React.useRef<NodeJS.Timeout>();
    const TabsRef = React.useRef<any>({} as any);
    const onPageScroll = () => {
        const matched_list = checkIntersection_anchorList(anchor_list, 86);
        // 
        let lastMatchedIndex = 0;
        matched_list.forEach((matched, index) => {
            if (matched) {
                lastMatchedIndex = index;
            }
        });
        const lastMatchedAnchor = anchor_list[lastMatchedIndex];
        if (!disable_scroll_linkage_timer.current) {
            TabsRef?.current?.switchToTabByCateId(lastMatchedAnchor?.firstCategoryId, lastMatchedAnchor?.secondCategoryId);
        }
    };

    React.useEffect(() => {
        window.addEventListener('scroll', onPageScroll);
        return () => {
            window.removeEventListener('scroll', onPageScroll);
        };
    }, undefined);
    // ===== onSpawn / onDestroy =====
    React.useEffect(() => {
        $get3rd(`back/api/store/${(routeInfo.routeParams as any)?.storeId}/menu.json`)
            .then((response) => {
                setRestMenuResponse(response);
            })
            .catch((err) => {
                console.log(err);
            });
        $get3rd(`back/api/store/${(routeInfo.routeParams as any)?.storeId}/restaurant.json`)
            .then((response) => {
                setRestResponse(response);
            })
            .catch((err) => {
                console.log(err);
            });
    }, []);
    // ===== event:  =====
    const TabSharedProps = React.useRef<TabSharedProps>();
    const onTabChange: OnTabChangeFuncType = (props, options) => {
        TabSharedProps.current = props;
    };
    // ===== event based on tab:  =====
    const generateOnClickTabItem = (firstCategoryId?: number, secondCategoryId?: number) =>
        function () {
            const anchor = anchor_list?.find((ac: any, i: number) => {
                const matchedFirst = String(ac.firstCategoryId) === String(firstCategoryId);
                if (matchedFirst) {
                    if (ac?.secondCategoryId) {
                        const matchedSecond = String(ac.secondCategoryId) === String(secondCategoryId);
                        if (matchedSecond) {
                            return true;
                        }
                        return false;
                    } else {
                        return true;
                    }
                }
                return false;
            });
            // 
            if (typeof anchor === 'object') {
                const el = document.getElementById(anchor?.elementId)?.parentElement;
                if (el?.offsetTop) {
                    if (disable_scroll_linkage_timer.current) {
                        clearTimeout(disable_scroll_linkage_timer.current);
                    }
                    // setIndex(anchor.index);
                    disable_scroll_linkage_timer.current = setTimeout(() => {
                        disable_scroll_linkage_timer.current = undefined;
                    }, 3000);
                    window.scroll({top: el?.offsetTop, behavior: 'smooth'});
                }
            }
        };
    const memoTabSectionOptions = React.useMemo(() => ({generateOnClickTabItem}), [anchor_list]);
    return (
        <Page>
            <MockStoreCoverSection store={RestResponse} />
            <StickyTopSection top={0} zIndex={5} style={{backgroundColor: 'white'}}>
                <TabSection tabsData={category_tab_data} initSelection={[0, 0]} TabsRef={TabsRef} onTabChange={onTabChange} options={memoTabSectionOptions} />
            </StickyTopSection>
            <WaterfallView items={waterfall_dish_list} render={MenuItemCard} columnGutter={5} columnWidth={180} overscanBy={200} />
        </Page>
    );
};

export default StorePage;

type AnchorType = {
    index: number;
    elementId: string;
    isAnchor: boolean;
};

const checkIntersection_anchorList = (anchors: AnchorType[], stickSectionHeight?: number) => {
    const res = anchors.map((ac) => checkIntersection_singleAnchor(ac.elementId, stickSectionHeight));
    return res;
};

const checkIntersection_singleAnchor = (elementId: string, stickSectionHeight?: number): number => {
    if (!stickSectionHeight) {
        return -1;
    }
    if (!elementId) {
        return -1;
    }
    // component context variables
    const el = document.getElementById(elementId);
    const anchorRect: undefined | DOMRect = el?.getBoundingClientRect();
    if (!anchorRect) {
        return -1;
    }
    if (anchorRect.top <= stickSectionHeight) {
        return 1;
    }
    return 0;
};
