import React, {CSSProperties} from 'react';
import Box, {BoxProps} from '@mui/material/Box';

const StickyTopSection: React.FC<BoxProps> = ({children, style = {}, top = undefined, ...rest}) => {
    const _style: CSSProperties = {
        position: 'sticky',
        ...style,
    };
    return (
        <Box style={_style} top={top} {...rest}>
            {children}
        </Box>
    );
};
export default StickyTopSection;
