/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable @typescript-eslint/restrict-template-expressions */
import React from 'react';
import {styled, SxProps, Tabs, TabsProps, Tab, TabProps} from '@mui/material';

type MainTabSectionPropsType = {
    data: any[] | undefined;
    selected: number;
    onChange: (event: React.SyntheticEvent<Element, Event>, value: any) => void;
    options?: {
        generateOnClickTabItem?: Function;
    };
};
const MainTabSection: React.FC<MainTabSectionPropsType> = (props) => {
    const {data, selected, onChange, options} = props;
    const onClickActions = data?.map((item) => {
        if (typeof options?.generateOnClickTabItem !== 'function') {
            return undefined;
        }
        return options.generateOnClickTabItem(item?.firstCategoryId, item?.secondCategoryId);
    });
    return (
        <StyledTabs value={selected} onChange={onChange} variant="scrollable">
            {data?.map((item, i, arr) => {
                return <StyledTab key={`first-tab-${item?.firstCategoryId}`} label={item.name} onClick={onClickActions?.[i]} />;
            })}
        </StyledTabs>
    );
};
export default React.memo(MainTabSection, (prev, next) => {
    return JSON.stringify(prev.data) === JSON.stringify(next.data) && prev.selected === next.selected && JSON.stringify(prev.options) === JSON.stringify(next.options);
});

const StyledTabs = styled((props: TabsProps) => <Tabs {...props} TabIndicatorProps={{children: <span className="MuiTabs-indicatorSpan" />}} />)(({theme}) => ({
    minHeight: '36px',
    height: '36px',
    marginBottom: '12px',
    '& .MuiTabs-indicator': {
        display: 'flex',
        height: 4,
        justifyContent: 'center',
        backgroundColor: 'transparent',
    },
    '& .MuiTabs-indicatorSpan': {
        maxWidth: 28,
        width: '100%',
        borderRadius: 2,
        backgroundColor: '#13C2C2',
    },
}));

const StyledTab = styled((props: TabProps) => <Tab disableRipple {...props} />)(({theme}) => ({
    color: theme.palette.common.black,
    fontWeight: theme.typography.fontWeightRegular,
    fontSize: theme.typography.pxToRem(14),
    margin: 0,
    padding: '0 10px',
    minWidth: 'auto',
    minHeight: '30px',
    boxSizing: 'border-box',
    height: '36px',
    lineHeight: '16px',
    '&.Mui-selected': {
        color: theme.palette.common.black,
        fontWeight: theme.typography.fontWeightBold,
    },
    '&.Mui-focusVisible': {
        backgroundColor: 'transparent',
    },
}));
