// NOTE: 

import React from 'react';
import {ContainerProps} from '@mui/material/Container';
import {Container, Box} from '@mui/material';
interface PageProps extends ContainerProps {
    headEl?: JSX.Element | null;
    footEl?: JSX.Element | null;
    isloading?: boolean;
}

const Page: React.FC<PageProps> = (props: PageProps) => {
    const {headEl, footEl, isloading = false, ...containerProps} = props;
    const containerStyle = {...(containerProps?.style ?? {}), padding: 0}

    return (
        <Container {...containerProps} style={containerStyle}>
            {React.isValidElement(headEl) ? headEl : null}
            {isloading ? null : ( // TODO: placeholder
                <Box>{containerProps?.children}</Box>
            )}
        </Container>
    );
};

export default Page;
